.contenedor-testimonio{
    width: 90%;
    height: 30%;
    margin: 10px 10px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    box-shadow: 0 3px 13px 1px rgb(0 0 0 / 9%);
}



.imagen-testimonio{
    height: 100%;
    width: 40%;
    object-fit: cover;
     float:inherit;
}

.contenedor-texto-testimonio{
    width: 100%;
    text-align: center;
    padding: 40px;
    font-family: Lato, sans-serif;
    font-weight: 400;
    line-height: 1.5rem;
    
}

.nombre-testimonio{
    font-size: 1.3rem;        /*rem toma el tamaño de la letra del elemento raiz, en este caso, habíamos asignado a html un font size de 18px, entonces aca el tamaño será 1.3 * 18 */
}

.cargo-testimonio{
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 30px;
}

.texto-testimonio{
    font-size: 1.1rem;
    text-align: justify;
}

@media screen and (max-width: 900px){
    .contenedor-testimonio{
        flex-direction: column;
    }
    .texto-testimonio{
        padding: 2em;
    }
}